.header .name {
	padding-top: 2%;
	margin-bottom: 5px;
	font-weight: bold;
	padding-left: 10%;
}

.header .job-title {
	padding-left: 10%;
}

.header .social {
	padding-left: 10%;
}

.coder {
	padding-top: 10%;
}

.social .icon {
	width: 36px;
	height: 36px;
	display: inline-block;
	border-radius: 50%;
	-moz-background-clip: padding;
	-webkit-background-clip: padding-box;
	background-clip: padding-box;
	color: #000000;
	text-align: center;
}

.social .icon:hover {
	background: #92c8ff;
	transform: scale(1.5);
}
.social .icon-name {
	color: #000000;
}
.social .icon-name:hover {
	text-decoration: underline;
	color: #000000;
}
.social .list-inline {
	display: inline;
	padding-right: 1%;
}

.social .list-inline .fab,
.social .list-inline .fas,
.social .fab,
.social .fas {
	font-size: 20px;
	margin-top: 8px;
}

.hero {
	margin-top: 10%;
	float: none;
	text-align: center;
	border-radius: 50%;
}

.nonscrollable {
	height: 100vh;
	overflow: hidden;
}
.scrollable {
	flex-grow: 1;
	height: 80vh;
	overflow-y: scroll;
	overflow-x: hidden;
	/* scrollbar-width: none;
	-ms-overflow-style: none; */
}
